import { getCurrentCart, ON_CART_FINISHED, ON_CART_PENDING, removeFromCart } from '@process-creative/slate-theme-tools';
import { generateLoader } from '../objects/loader';
import { CLASS_ACTIVE, CLASS_VISIBLE } from '../tools/constants/classes';
import { short } from '../settings/animation';
import { delegate } from './../tools/utilities/event-tools';
import { Cart } from '../tools/cart/cart';
import { initCarousel } from '../objects/carousel';
import KeenSlider from 'keen-slider';

import '../objects/upsell';
import { getElem } from '../tools/utilities/helper-functions';
import { removeBundleFromCart } from '../objects/bundle/bundle-common';

const CLASS_CART_OPEN = 'is-ajax-cart-open';

const SELECTOR_CONTAINER = '[data-ajax-cart]';
const SELECTOR_LINES_CONTAINER = '[data-ajax-cart-lines]';
const SELECTOR_LOADER = '[data-ajax-cart-loader]';
const SELECTOR_REMOVE = '[data-ajax-cart-remove]';
const SELECTOR_PRODUCTS = '[data-ajax-cart-upsell]';


let isSliding = false;

/**
 * Show the Cart Drawer on the page.
 * 
 * @returns A promise that resolves when the cart drawer is visible.
 */
export const cartDrawerShow = () => new Promise<boolean>((resolve) => {
  if(isSliding) return resolve(false);

  const container = document.querySelector(SELECTOR_CONTAINER);
  if(!container) return resolve(false);

  isSliding = true;

  container.classList.add(CLASS_ACTIVE);
  document.body.classList.add(CLASS_CART_OPEN);
  reInitCarousels();

  setTimeout(() => {
    container.classList.add(CLASS_VISIBLE);
    isSliding = false;
  }, 16);

  setTimeout(() => resolve(true), short);
});

/**
 * Hide the Cart Drawer off the page.
 * 
 * @returns A promise that resolves when the cart drawer is no longer visible.
 */
export const cartDrawerHide = () => new Promise<boolean>((resolve) => {
  if(isSliding) return resolve(true);
  const container = document.querySelector(SELECTOR_CONTAINER);
  if(!container) return resolve(false);
  isSliding = true;

  container.classList.remove(CLASS_VISIBLE);
  document.body.classList.remove(CLASS_CART_OPEN);
  setTimeout(() => {
    container.classList.remove(CLASS_ACTIVE);
    isSliding = false;
    resolve(true);
  }, short);
});

/**
 * Is the cart drawer currently visible?
 * @returns True if visible, otherwise false.
 */
export const cartDrawerIsVisible = () => {
  return document.querySelector(SELECTOR_CONTAINER)?.classList.contains(CLASS_ACTIVE);
}

/**
 * Toggle the cart drawer between open and closed.
 * 
 * @returns A promise that resolves when the cart drawer has toggled.
 */
export const cartDrawerToggle = () => (
  cartDrawerIsVisible() ? cartDrawerHide() : cartDrawerShow()
);


// Listen for cart events
let cartPending = false;
let sectionPending = false;
const pendingStateUpdate = () => {
  const conts = document.querySelectorAll<HTMLDivElement>(SELECTOR_LINES_CONTAINER);
  const isPending = cartPending || sectionPending;

  const strLoader = generateLoader({
    container: true,
    attributes: SELECTOR_LOADER.replace(/\[|\]/g, ''),
    clazz: 'c-ajax-cart__loader'
  });

  // Add Loader
  conts.forEach(container => {
    if(isPending) {
      const elem = document.createElement('div');
      elem.innerHTML = strLoader;
      container.appendChild(elem.children[0]);
      elem.remove();
    } else {
      container.querySelector<HTMLElement>(SELECTOR_LOADER)?.remove();
    }
  });
}


$(document).on(ON_CART_PENDING, () => {
  cartPending = true;
  pendingStateUpdate();
});


function determineOpenCloseDrawer(){
  const isNotCartPage = !location.pathname.endsWith('/cart')
  const cartItems = (getCurrentCart() as Cart).items;
  cartItems.length >= 1 && isNotCartPage ? cartDrawerShow() : cartDrawerHide()
}

let carousels:KeenSlider[] = [];
const reInitCarousels = () => {
  carousels.forEach(c => c.destroy());
  document.querySelectorAll<HTMLDivElement>(SELECTOR_PRODUCTS).forEach(e => {
    const slider = initCarousel({
      container: e,
      options: {
        slidesPerView: 1,
        loop: true,
        spacing: 16
      },
    });
    if(slider) carousels.push(slider.slider);
  });
}

$(document).on(ON_CART_FINISHED, async () => {
  determineOpenCloseDrawer()
  cartPending = false;
  sectionPending = true;
  
  pendingStateUpdate();

  const original = getElem('[data-ajax-cart]')!

  const updated = await getUpdatedCartTemplate()
  original.innerHTML = updated.innerHTML

});

function getUpdatedCartTemplate(): Promise<HTMLElement>{
  return fetch('/?sections=ajax-cart')
  .then(e => e.json())
  .then(data => { 
    const child = data['ajax-cart'] as string;
    const dummy = document.createElement('div');
    dummy.innerHTML = child;
    const updated = getElem('[data-ajax-cart]', dummy)!;
    return updated;
  });
}

// Show cart drawer on toggle click
delegate({
  element: document,
  selector: '[data-cart-drawer-toggle]',
  event: 'click',
  callback: e => {
    e.preventDefault();
    cartDrawerToggle();
  }
})

delegate({
  element: document,
  selector: '[data-ajax-cart-close]',
  event: 'click',
  callback: e => {
    e.preventDefault();
    cartDrawerHide();
  }
})

delegate({
  element: document,
  selector: SELECTOR_REMOVE,
  event: 'click',
  callback: (e, target) => {
    let lineIndex = parseInt(target.getAttribute('data-index') || '');
    if(isNaN(lineIndex) || !isFinite(lineIndex)) return;

    e.preventDefault();
    
    const cartItems = (getCurrentCart() as Cart)?.items;
    const item = cartItems[lineIndex]
    if(item.properties?.isProductBundle){
      const bundleId = item.properties?.isProductBundle
      removeBundleFromCart(bundleId)
    }else{
      removeFromCart(lineIndex+1);
    }
  }
});